.about_container{
    max-width: 70vw;
    width: 100%;
    /* height: 500px; */
    height: 100%;
    /* height: 30vw; */
    margin-left: auto;
    margin-right: auto;
    flex: 1 auto;
    /* padding-top: 35px; */
}
.about_header{
    width: 100%;
    height: 50px;
}
.about_body{
    width: 70%;
    height: 90%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-left: auto;
    margin-right: auto; 
}


.about_description{
    width: 100%;
    height: 100%;
    max-height: inherit;
    margin: auto;
    /* padding: 0px 0px 0px 1vw; */
    font-size: 16px;
    display: inline-block;
    max-width: 90vw;
    padding: 10px;
    /* word-break: break-all; optional */
}
.about_options{
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    margin-right: 10%;
}
/* ABOUT MENU ICONS */
.about_icon{
    width: 100%;
    height: 50px;
    /* background-color: white; */
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    align-items: center;
    border: solid black 0.3px;
    /* border-bottom: dashed rgba(128,128,128,0.2); */
}
.about_language{
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: white;
    /* border-right: solid black; */
}
.about_language >*{
    text-transform: uppercase;
    font-size: 13px;
}
.about_flag{
    width: 40%;
    height: 100%;
    /* background-color: var(--secondaryColor3); */
}

/* MOBILE RESPONSIVE */
@media only screen and (max-width: 768px){
    .about_body{
        flex-direction: column;
        padding-top: 0px;
        margin: 0;
        width: 100%;
    }
    .about_options{
        width: 100%;
        height: 20%;
    }
    .about_description{
        width: 100%;
        height: 80%;
        font-size: 14px;
    }
}


/* SPECIAL EFFECTS */
.basketball_icon{
    /* position: relative; */
    width: 100px;
    height: 100px;
    background-color: grey;
}
.basketball_icon:hover{
    background-image: url('../assets/Ire_outline_b.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 300% 300%;
    cursor: cell;
    background-color: grey;
    color: red;
}









